export default function Services() {
    const items = [
        { t: 'E-commerce Architecture', d: 'ماژولار، SLA، Observability، آماده ترافیک بالا' },
        { t: 'Backend APIs', d: 'Django/DRF و Go؛ امنیت، کارایی، پایداری' },
        { t: 'Frontend Experience', d: 'Vue/React/Next؛ تجربه سریع و پایدار' },
        { t: 'DevOps & CI/CD', d: 'Docker، GitHub Actions، Cloudflare، Monitoring' },
    ];
    return (
        <section id="services" className="container py-16 md:py-24">
            <h2 className="text-2xl md:text-3xl font-bold mb-8">تخصص‌ها</h2>
            <div className="grid sm:grid-cols-2 lg:grid-cols-4 gap-6">
                {items.map((it) => (
                    <div key={it.t} className="glass p-6 hover:bg-white/10 transition">
                        <div className="text-lg font-semibold mb-2">{it.t}</div>
                        <div className="text-white/70 text-sm leading-relaxed">{it.d}</div>
                    </div>
                ))}
            </div>
        </section>
    );
}
