import Link from 'next/link';

export default function Footer() {
    return (
        <footer id="contact" className="mt-12 border-t border-white/10 bg-black/30">
            <div className="container py-10 flex flex-col md:flex-row items-center justify-between gap-4">
                <div className="text-white/70 text-sm">© {new Date().getFullYear()} Cybernith</div>
                <div className="flex items-center gap-4 text-sm">
                    <Link href="mailto:hello@your-domain.dev" className="hover:text-white">hello@your-domain.dev</Link>
                    <Link href="https://github.com/your-handle" className="hover:text-white">GitHub</Link>
                    <Link href="https://www.linkedin.com/in/your-handle" className="hover:text-white">LinkedIn</Link>
                </div>
            </div>
        </footer>
    );
}
