import projects from '@/content/projects.json';

export default function Projects() {
    return (
        <section id="work" className="container py-16 md:py-24">
            <h2 className="text-2xl md:text-3xl font-bold mb-8">کارهای منتخب</h2>
            <div className="grid md:grid-cols-3 gap-6">
                {projects.map((p) => (
                    <article key={p.title} className="glass p-6">
                        <h3 className="font-semibold text-lg mb-2">{p.title}</h3>
                        <p className="text-sm text-white/80"><b>مسئله:</b> {p.challenge}</p>
                        <p className="text-sm text-white/80 mt-1"><b>راه‌حل:</b> {p.solution}</p>
                        <p className="text-sm text-white/90 mt-2"><b>اثر:</b> {p.impact}</p>
                    </article>
                ))}
            </div>
        </section>
    );
}
